import Layout from "../components/LayoutComponent";
import WeeklySummary from "../components/WeeklySummaryComponent";
import IncomeChart from "../components/IncomeChartComponent";
import ExpenseChart from "../components/ExpenseChartComponent";
import SavingsPrediction from "../components/SavingsPredictionComponent";

const Dashboard = () => {
  const savings = 500;

  return (
    <Layout>
      <h2 className="text-3xl font-bold mb-4 text-center">Dashboard</h2>
      <p className="text-lg text-center mb-6 text-gray-700">
        Selamat datang di dashboard aplikasi penghitung pengeluaran dan
        pemasukan!
      </p>

      <div className="mb-8">
        <WeeklySummary />
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 mb-8">
        <div className="bg-white shadow-md rounded-lg p-4">
          <h3 className="text-2xl font-semibold mb-4">Grafik Pemasukan</h3>
          <IncomeChart />
        </div>

        <div className="bg-white shadow-md rounded-lg p-4">
          <h3 className="text-2xl font-semibold mb-4">Grafik Pengeluaran</h3>
          <ExpenseChart />
        </div>
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">Prediksi Tabungan</h3>
        <SavingsPrediction savings={savings} />
      </div>
    </Layout>
  );
};

export default Dashboard;
